@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;600&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*background: url(./assets/images/84303.jpg) no-repeat fixed;
  backdrop-filter: grayscale(0.5) opacity(0.4);*/
  font-family: 'Orbitron', sans-serif;
  background-color: black;
}
.loading-logo{
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 10px;
  color: white;
  height: 100%;
}
h1 {
  margin-top: 45px;
  color:#DEB841;
  text-align: center;
}
p {
  letter-spacing: 1px;
}

.date-input {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
#date {
  margin-left: 10px;
}
.pic-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-items:center;
          align-items:center;
  margin: 20px 0 20px 0;
}
img, .explanation {
  max-width: 100%;
  margin: 20px 0;
}

.explanation {
  text-align: justify;
  color: rgba(255,255,255,0.6)
}
.iframe-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-top: 56.25%;

}
.video {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}


@media screen and (min-width: 1024px) {
  .form {
    position: relative;
  }
  img, .explanation {
    max-width: 800px;
    
  }
  h1 {
    margin: 0;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    position: absolute;
    left: -325px;
    top: 450px; 
  }
}


@media screen and (min-width: 1444px) {
  .content-container {
    display: -webkit-flex;
    display: flex;
    width: 1400px;
    -webkit-align-items: center;
            align-items: center;
  }
  .desc-container{
    margin-left: 20px;
  }
  .explanation {
    font-size: 19px;
  }
  .form {
    margin-top: 50px;
    position: relative;
  }
  .pic-items {
    margin: 50px 0 80px 0;
  }
 
}
